								</view>
								
								<view>
									<view class="mtwo"><input placeholder="如：8元" v-model="fourth" type="digit" v-input-limit='0' maxlength="6"/><span class="mtwotxt">元</span></view>
									<view class="mtwo"><span class="mtwotxt" style='margin-right: 10upx;margin-left: 0upx;margin-top:0upx'>优惠率:</span><input v-model="disFourth" type="digit" v-input-limit='2' maxlength="4" style="width: 156upx;margin-top:20upx;margin-bottom:20upx"/></view>
								</view>
								
							</view>
							<view class="monthand" style="color: red;font-size: 26upx;padding:30upx 40upx ;">
								优惠率：填值范围0-1，最多2位小数。此优惠为延长用户的充电时间;如费率配置为：0瓦 以上 1小时，优惠率0.5，用户选择充2元，则用户可充电时长为3小时；
							</view>
						</view>
						<view class="stap">
							<view class="staptxt">计时：</view>
							<view class="stapsix" @click="yyshowopen">添加档位</view>
							<view class="stapone1"  v-for="(item, index) in fourlist" :key="index">
								<view class="stapone1left">
									{{item.left}}-{{item.right}}瓦：
								</view>
								<view class="stapone1z">
									1元/{{item.money}}小时
								</view>
								<view class="stapone1right" @click="shanbtn(index)" v-show="index==fourlist.length-1">
									删除
								</view>
							</view>
							<view class="stapseven1" >
								<view class="stapsevenleft" >{{smoney2}}瓦以上：</view>
								1元 /<input v-model="fourhat" type="digit" placeholder="小时" style="width: 320upx; margin-left: 15upx;"/>
								<view class="stapsevenleft1">小时</view>
							</view>
							<view class="stapone" style="margin-top: 0rpx;margin: 32rpx 0;width: 87%;">
								<view class="staponeleft" style="width: 38%;margin-top: 0rpx;">
									计费单位（分钟）：[1-60且必须为整数]
								</view>
								<input v-model="onecharging" type="number" v-input-limit='0'>
							</view>
							
							<view class="sfant" v-show="fourtype==2">
								<van-checkbox v-model="checked" shape="square">允许退费</van-checkbox>
							</view>
							<view class="sfant" v-show="fourtype==2">
								<van-checkbox v-model="Stopchecked" shape="square">允许充满自停</van-checkbox>
							</view>
							<view class="stapseven" v-show="fourtype==2&&checked">
								<view class="stapsevenleft">最低消费：</view>
								<input v-model="minMoney" type="digit"/>
								<view class="stapsevenleft1">元</view>
							</view>
							<view class="stapone" v-show="fourtype==2&&occNum>0">
								<view class="staponeleft" >
									占位费封顶金额：
								</view>
								<input  v-model="occupyMax" type="digit"/>
								<view class="staponelefts">
									元
								</view>
							</view>
							<view class="stapone" v-show="fourtype==2&&occNum>0">
								<view class="staponeleft" >
									占位费率：
								</view>
								<input  v-model="occupyRate" type="digit" />
								<view class="staponelefts">
									
								</view>
							</view>
							<view class="stapone" v-show="fourtype==2&&occNum>0">
								<view class="staponeleft" >
									免费时长(小时)：
								</view>
								<input  v-model="occupyFree" type="digit"/>
								<view class="staponelefts">
									小时
								</view>
							</view>
							<view class="staptwo" v-show="fourtype==2">
								<view class="staptwohand">备注：（100字）</view>
								<textarea maxlength='100'v-model="remarks"/>
							</view>
							<view class="stapthree">
								<!-- <view class="stapthreeleft">保存</view>
								<view class="stapthreeright">删除</view> -->
								<view class="stapthreezhong" @click="xinzbtn">保存</view>
							</view>
							<view class="stapfour" v-show="fourtype==2" @click="fourbtn(1)">
								<view class="stapfourhand">
									<image src="../../static/tabBarimg/up.png"></image>
								</view>
								<view class="stapfourfoot">收起更多选填参数</view>
							</view>
							<view class="stapfour" @click="fourbtn(2)" v-show="fourtype==1">
								<view class="stapfourfoot">展开更多选填参数</view>
								<view class="stapfourhand">
									<image src="../../static/tabBarimg/down.png"></image>
								</view>
							</view>
							<view class="stapfive" v-show="fourtype==2">
								<view class="stapfivehand">说明:</view>
								<view class="stapfivefoot">
									<!-- 最低消费:填0表示不设最低消费;<br/>
									电损:适当向用户收取设备的电量损耗，填0.01-1之间的小数; 填0表示不设电损:</br> -->
									备注:可以在"用户扫码-查看费率"中显示，100字以内;<br/>
									<span style='color: red;'>
										计费单位：以充电时长62分钟为例：<br/>
										1、如计费单位使用默认的30分钟，则按用户充电90分钟结算；<br/>
										1、如设置计费单位为60分钟，则按用户充电120分钟结算；<br/>
										2、如设置计费单位为20分钟，则按用户充电80分钟结算；<br/>
										备注：为了减少纠纷，系统有一个一分钟的缓冲区，超过计费单位1分钟以内的将会被忽略。
									</span>
								</view>
							</view>
						</view>
					
					</view>
				</van-tab>
				
			</van-tabs>
		</view>
		<!-- //新增档位弹窗 -->
		<van-popup v-model="addshow">
			<view class="tan">
				<view class="tanhand">添加档位</view>
				<view class="tanone">
					<span class="tantxt">{{onevul}}瓦</span>
					<view class="tanonetap"></view>
					<input  v-model="twovul" type="digit"/><span class="tantxt">瓦</span>
				</view>
				<view class="tantwo">
					<input placeholder="金额" v-model="moneyvul" type="digit"/><span class="tantxt">元</span>
				</view>
				<view class="tanbtn">
					<view class="tanbtnleft" @click="addshowclose">取消</view>
					<view class="tanbtnright" @click="addbtn">保存</view>
				</view>
			</view>
		</van-popup>
		<!-- //新增服务费弹窗  -->
		<van-popup v-model="addservices">
			<view class="tan">
				<view class="tanhand">添加档位</view>
				<view class="tanone">
					<input  v-model="avul" type="digit"/><span class="tantxt">天</span>
					<view class="tanonetap"></view>
					<input  v-model="bvul" type="digit"/><span class="tantxt">元</span>
				</view>
				<view class="tanbtn">
					<view class="tanbtnleft" @click="mclose">取消</view>
					<view class="tanbtnright" @click="mbtn">保存</view>
				</view>
			</view>
		</van-popup>
		<!-- //一元新增档位弹窗 -->
		<van-popup v-model="yiyuanshow">
			<view class="tan">
				<view class="tanhand">添加档位</view>
				<view class="tanone">
					<span class="tantxt">{{yyonevul}}瓦</span>
					<view class="tanonetap"></view>
					<input  v-model="yytwovul" type="digit"/><span class="tantxt">瓦</span>
				</view>
				<view class="tantwo" style="width: 300upx;">
					<span class="tantxt" style='margin-left: 0upx;margin-right: 10upx;'>1元/</span><input placeholder="时长" v-model="yyvul" type="digit"/><span class="tantxt">小时</span>
				</view>
				<view class="tanbtn">
					<view class="tanbtnleft" @click="yiyuanclose">取消</view>
					<view class="tanbtnright" @click="yyaddbtn">保存</view>
				</view>
			</view>
		</van-popup>
	</view>
</template>

<script>
	import tou_hand1 from '../../components/tou_hand1/tou_hand1.vue'
	import {section} from "../../common/section.js"
	import Toast from '../../wxcomponents/vant/toast/toast';
	import Dialog from '../../wxcomponents/vant/dialog/dialog';
	export default {
		components: {tou_hand1},
		mounted() {
			 
		},

		data() {
			return {
				active: '',
				twolist:[],
				threelist:[],
				fourlist:[{'left':0,'right':140,'money':4},{'left':141,'right':280,'money':3},{'left':281,'right':400,'money':2.5}],
				projectId:"",//小区ID
				onetype:1,
				twotype:1,
				threetype:1,
				fourtype:1,
				addshow:false,//新增档位弹窗
				onevul:'0',//弹窗第一个值
				twovul:'',//弹窗第二个值
				moneyvul:'',//弹窗金额
				yyonevul:'',//一元弹窗第一个值
				yytwovul:'',//一元弹窗第一个值
				yyvul:'',//一元弹窗时长
				smoney:0,//多少瓦以上
				smoney1:0,//多少瓦以上
				smoney2:0,//多少瓦以上
				packageName:'',//套餐名
				type:'B2',//计费类型（A包月【A1包时，A3包次】，B常用模式【B1计时，B2计量，B3计次，B5计量2】, C临时【C1临时计时】）
				standard:'',//计费金额
				num:0,//服务费费率
				minMoney:0,//最低消费
				lossRate:0,//电损率
				remarks:'',//备注
				twohat:'',
				threehat:'',
				fourhat:2,
				packageName:'',//套餐名
				first:'',//临时金额1
				second:'',//临时金额2
				third:'',//临时金额3
				fourth:'',//临时金额4
				remarks:'',//备注
				checked: false,
				yiyuanshow:false,//一元弹窗展示
				disFirst:0,//一元第一档费率
				disSecond:0,//一元第二档费率
				disThird:0,//一元第三档费率
				disFourth:0,//一元第三档费率
				occupyMax:0,//占位费封顶金额（充电柜）
				occupyRate:0.3,//占位费率（充电柜）
				occupyFree:2,//免费时长（充电柜）-小时
				occNum:0,//充电柜充电区数量
				Stopchecked:false,//是否允许充满自停
				jscharging:30,//计时计费
				onecharging:30,//一元计费
				checkedtap:false,//服务费套餐
				services:[],//服务费套餐列表
				addservices:false,//服务费套餐添加
				avul:0,//服务费套餐天数
				bvul:0,//服务费套餐金额
				dayNum:0,//一天内最多可充次数
				limitNum:0,//允许同时充电最大次数
				
			}
		},
		onLoad(options) {
			this.projectId=options.projectId;
			this.occNum=options.occNum
		},
		methods: {
			
			onebtn(vul){
				this.onetype=vul
			},
			twobtn(vul){
				this.twotype=vul
			},
			threebtn(vul){
				this.threetype=vul
			},
			fourbtn(vul){
				this.fourtype=vul
			},
			//关闭新增档位弹窗
			addshowclose() {
				
				// this.onevul='',
				this.twovul='',
				this.moneyvul=''
				this.addshow = false
			},
			mclose(){
				this.avul='',
				this.bvul=''
				this.addservices = false
			},
			//关闭新增一元档位弹窗
			yiyuanclose(){
				this.yytwovul='',
				this.yyvul=''
				this.yiyuanshow = false
			},
			addopenservice(){
				this.addservices=true
				if(this.services.length>0){
					this.avul=Number(this.services[this.services.length-1].days)+1
					
				}else{
					this.avul='0'
				}
			},
			//打开新增档位弹窗
			addshowopen() {
				if(this.active==2){
					if(this.twolist.length>0){
						this.onevul=Number(this.twolist[this.twolist.length-1].right)+1
						
					}else{
						this.onevul='0'
					}
				}else if(this.active==3){
					if(this.threelist.length>0){
						this.onevul=Number(this.threelist[this.threelist.length-1].right)+1
						
					}else{
						this.onevul='0'
					}
				}
				
				
				this.addshow = true
			},
			//清空档位
			delshowopen(){
				if (this.active == 2) {
					this.twolist=[]
					this.onevul = '0'
					this.smoney = '0'
					this.twohat = 0
					
				} else if (this.active == 3) {
					this.threelist=[]
					this.onevul = '0'
					this.smoney1 = '0'
					this.threehat = 0
					
				}else if(this.active == 0){
					this.services=[]
				}else if(this.active == 1){
					this.services=[]
				}
			},
			yyshowopen(){
				if(this.fourlist.length>0){
					this.yyonevul=Number(this.fourlist[this.fourlist.length-1].right)+1
					
				}else{
					this.yyonevul='0'
				}
				this.yiyuanshow = true
			},
			//新增
			addbtn(){
				if(!this.onevul||!this.twovul||!this.moneyvul){
					this.$toast.fail('请完善信息')
					return
				}
				if(this.onevul>this.twovul||this.onevul==this.twovul){
					this.$toast.fail('第一档不能小于或等于第二档')
					return
				}
				if(this.active==2){
					if(Number(this.moneyvul)<0||Number(this.moneyvul)>5){
						this.$toast.fail('金额应在0~5元之间')
						return
					}
				}else if(this.active==3){
					if(Number(this.moneyvul)<0||Number(this.moneyvul)>20){
						this.$toast.fail('金额应在0~20元之间')
						return
					}
				}
				if(section(this.moneyvul)==true){
					Dialog.confirm({
					  title: '提示',
					  message: '系统检测到有非正常范围内（正常为0.2-3元）的费率金额，点击“取消”进行检查，点击“确认”继续操作！'
					}).then(() => {
					 var arr={left:this.onevul,right:this.twovul,money:this.moneyvul}
					 if(this.active==2){
					 	this.twolist.push(arr)
					 	this.smoney=Number(this.twolist[this.twolist.length-1].right)+1
					 }else if(this.active==3){
					 	this.threelist.push(arr)
					 	this.smoney1=Number(this.threelist[this.threelist.length-1].right)+1
					 }
					 
					 this.addshowclose();
					}).catch(() => {
						return
					});
				}else{
					var arr={left:this.onevul,right:this.twovul,money:this.moneyvul}
					if(this.active==2){
						this.twolist.push(arr)
						this.smoney=Number(this.twolist[this.twolist.length-1].right)+1
					}else if(this.active==3){
						this.threelist.push(arr)
						this.smoney1=Number(this.threelist[this.threelist.length-1].right)+1
					}
					
					this.addshowclose();
				}
				
			},
			//一元新增
			yyaddbtn(){
				if(!this.yyonevul||!this.yytwovul||!this.yyvul){
					this.$toast.fail('请完善信息')
					return
				}
				if(this.yyonevul>this.yytwovul||this.yyonevul==this.yytwovul){
					this.$toast.fail('第一档不能小于或等于第二档')
					return
				}
				
					var arr={left:this.yyonevul,right:this.yytwovul,money:this.yyvul}
					this.fourlist.push(arr)
					this.smoney2=Number(this.fourlist[this.fourlist.length-1].right)+1
					this.yiyuanclose();
				
				
			},
			mbtn(){
				if(!this.avul||!this.bvul||this.avul==0||this.bvul==0){
					this.$toast.fail('请完善信息')
					return
				}
				if(this.avul<0||this.bvul<0){
					this.$toast.fail('请输入整数')
					return
				}
				if(this.avul>365){
					this.$toast.fail('天数不能大于365')
					return
				}
				let maxmoney=this.avul*3
				if(this.bvul>maxmoney){
					this.$toast.fail('金额不能大于'+maxmoney)
					return
				}
				var arr={days:this.avul,rates:this.bvul}
				this.services.push(arr)
				this.mclose()
			},
			//删除
			shanbtn(vul){
				if(this.active==2){
					this.twolist.splice(vul,1)
					if(this.twolist.length>0){
						this.smoney=Number(this.twolist[this.twolist.length-1].right)+1
						
					}else{
						this.smoney='0'
					}
				}else if(this.active==3){
					this.threelist.splice(vul,1)
					if(this.threelist.length>0){
						this.smoney1=Number(this.threelist[this.threelist.length-1].right)+1
						
					}else{
						this.smoney1='0'
					}
				}else if(this.active==4){
					this.fourlist.splice(vul,1)
					if(this.fourlist.length>0){
						this.smoney2=Number(this.fourlist[this.fourlist.length-1].right)+1
						
					}else{
						this.smoney2='0'
					}
				}else if(this.active==0){
					this.services.splice(vul,1)
				}else if(this.active==1){
					this.services.splice(vul,1)
				}
			},
			onClick(){
				this.packageName=''
				this.minMoney=0
				this.occupyMax=0
				this.occupyRate=0.3
				this.occupyFree=2
				this.lossRate=0
				this.remarks=''
				this.twolist=[]
				this.threelist=[]
				this.foutlist=[{'left':0,'right':140,'money':4},{'left':141,'right':280,'money':3},{'left':281,'right':400,'money':2.5}]
				this.standard=0
				this.num=''
				this.twohat=''
				this.smoney=0
				this.smoney1=0
				this.smoney2=Number(this.fourlist[this.fourlist.length-1].right)+1
				this.threehat=''
				this.fourhat=2
				this.disFirst=0
				this.disSecond=0
				this.disThird=0
				this.disFourth=0
			},
			//添加一个费率
			xinzbtn(){
				let data={}
				if(Number(this.occupyMax)>9999){
					this.$toast.fail('占位费上限9999元')
					return
				}
				if(this.active==0){
					let arr=[]
					let map={}
					map['standard']=this.standard
					map['num']=Number(this.num)
					arr.push(map)
					if(!this.packageName){
						this.$toast.fail('套餐名不能为空')
						return;
					}
					if(!this.standard.toString()){
						this.$toast.fail('套餐金额不能为空')
						return
					}
					if(this.num<0||this.num>3){
						this.$toast.fail('服务费费率应在0-3之间')
						return
					}
					if(Number(this.standard)<0||Number(this.standard)>3){
						this.$toast.fail('套餐金额应在0-3之间')
						return
					}
					if(this.limitNum>10){
						this.$toast.fail('同时充电最大数量不能大于10')
						return
					}
					if(this.dayNum>10){
						this.$toast.fail('一天内最多可充次数不能大于10')
						return
					}
					data={
						packageName:this.packageName,
						type:'B2',
						minMoney:this.minMoney,
						occupyMax:this.occupyMax,
						occupyRate:this.occupyRate,
						occupyFree:this.occupyFree,
						lossRate:this.lossRate,
						isFund:0,
						isAutoStop:0,
						steps:arr,
						remarks:this.remarks,
						services:this.services,
						limitNum:this.limitNum,
						dayNum:this.dayNum,
						defaultAutoStop: 0
					}
					
				}else if(this.active==1){
					let arr=[]
					let map={}
					map['standard']=this.standard
					map['num']=Number(this.num)
					arr.push(map)
					if(!this.packageName){
						this.$toast.fail('套餐名不能为空')
						return;
					}
					if(!this.standard.toString()){
						this.$toast.fail('套餐金额不能为空')
						return
					}
					if(this.num<0||this.num>3){
						this.$toast.fail('服务费费率应在0-3之间')
						return
					}
					if(Number(this.standard)<0||Number(this.standard)>3){
						this.$toast.fail('套餐金额应在0-3之间')
						return
